/* Tiny reset thingy */
body,html{margin:0;padding:0;}

.wrapper {
  /* The height needs to be set to a fixed value for the effect to work.
   * 100vh is the full height of the viewport. */
  height: 100vh;
  /* The scaling of the images would add a horizontal scrollbar, so disable x overflow. */
  overflow-x: hidden;
  /* Enable scrolling on the page. */
  overflow-y: auto;
  /* Set the perspective to 2px. This is essentailly the simulated distance from the viewport to transformed objects.*/
  -webkit-perspective: 2px;
          perspective: 2px;
}

.section {
  /* Needed for children to be absolutely positioned relative to the parent. */
  position: relative;
  /* The height of the container. Must be set, but it doesn't really matter what the value is. */
  /*height: 100%;*/
}

.parallax::after {
  /* Display and position the pseudo-element */
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  
  /* Move the pseudo-element back away from the camera,
   * then scale it back up to fill the viewport.
   * Because the pseudo-element is further away, it appears to move more slowly, like in real life. */
  -webkit-transform: translateZ(-1px) scale(1.5);
          transform: translateZ(-1px) scale(1.5);
  /* Force the background image to fill the whole element. */
  background-size: 100%;
  /* Keep the image from overlapping sibling elements. */ 
  z-index: -1;
}

.bg1::after {
  background-image: url(/static/media/bananatree.7eb99ad0.jpeg);
  background-size: cover;
  margin-top: -100px;
}

.bg2::after {
  background:
    linear-gradient(
      rgba(255, 255, 255, .2),
      rgba(255, 255, 255, .2)),
    url(/static/media/paint.80a2858a.jpeg);
  background-size: cover;
}


.about-me {
  border-top: 15px solid rgba(234, 202, 187);
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 0 0 5%;
  background-color: #fff;
  height: 500px;
}

.am-text-container {
  width: 60%;
  text-align: right;
  margin-right: 5%;
}

.am-me-icon {
  height: 300px;
  width: 300px;
  background-image: url(/static/media/pic.73c90fa0.jpg);
  background-size: cover;
}

@media only screen and (max-width: 720px) {
  #about-me {
    border-top: 10px solid rgba(234, 202, 187);
    padding: 0px 5% 20px;
  }

  .am-text-container {
    width: 100%;
    text-align: left;
    margin-right: 0;
  }

  .am-title {
    font-size: 1.2rem;
  }

  .am-me-icon {
    display: none;
  }
}
.s-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 50px 5% 80px;
  background-size: cover;
}

.s-skills {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(205px, 1fr));
}

.s-skill {
  border-top: 15px solid #eacabb;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px 20px 10px;
}

.s-skill-item {
  margin: 5px 0;
}

.s-title {
  margin: 0 0 20px;
  letter-spacing: 5px;
  color: #fff;
  font-size: 3rem;
  text-transform: uppercase;
}

.s-subheading {
  letter-spacing: 5px;
  margin: 20px 0 0;
}

.s-icon {
  width: 80px;
  height: 80px;
  margin: 20px 0 40px;
}

@media only screen and (max-width: 720px) {
  .s-container {
    padding: 40px 5%;
    background:
      linear-gradient(
        rgba(255, 255, 255, .8),
        rgba(255, 255, 255, .8)),
      url(/static/media/paint.80a2858a.jpeg);
    background-size: cover;
  }
  .s-title {
    margin: 0 0 10px;
    letter-spacing: 5px;
    font-size: 1.2rem;
  }
}
.pp-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.pp-projects-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 50px 0 0 0;
  width: 100%;
  background-color: #fff;
}

.pp-project-container {
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1%;
}

.pp-title {
  color: #fff;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: normal;
}

.pp-icon {
  width: 4%;
  margin: 5%;
}

.pp-icon:hover {
  cursor: pointer;
}


.p-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}

.p-project-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.p-title {
  text-transform: uppercase;
  text-align: center;
  margin: 0 0 2%;
  letter-spacing: 7px;
}

.p-images {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.p-project-img {
  width: 40%;
  margin: 0 auto;
}

.p-text {
  text-align: center;
  margin: 2% 20%;
}

.p-tech-icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.p-tech-icon {
  height: 30px;
  margin: 10px;
}

.p-btns {
  display: flex;
  justify-content: center;
  width: 60%;
}

.p-btn {
  background: rgba(222, 172, 148, .7);
  color: #FFF;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2px;
  transition: .5s;
  width: 30%;
  padding:.7%;
}

.p-btn:hover {
  background: rgba(222, 172, 148);
}

@media only screen and (max-width: 650px) {
  .p-title {
    font-size: 1rem;
  }
  .p-project-img {
    width: 90%;
  }

  .p-tech-icon {
    height: 20px;
    margin: 5px;
  }

  .p-text {
    text-align: center;
    margin: 2% 5%;
  }
}
* {
  font-family: 'Raleway', sans-serif;
  color: #5f5f5f;
}

body {
  line-height: 1.4;
  letter-spacing: 1px;
  margin: 0;
}
